.notfound {
    margin: 0 auto;
    min-height: 400px;
    height: 1vh;
    max-width: 1280px;
    padding: 60px 0;
    display: grid;
  
  }
  
  .notfound__title {
    margin: 0;
    font-size: 140px;
    line-height: 169px;
    text-align: center;
    font-weight: 400;
  }
  
  .notfound__subtitle {
    margin: 0;
    margin-top: 5px;
    font-size: 16px;
    line-height: 19px;
    text-align: center;
  }
  
  .notfound__btn {
    margin: 60px auto;
    font-size: 14px;
    line-height: 17px;
    color: #9090a0;
    border: 0;
    background: none;
    cursor: pointer;
  }
  
  .notfound__img {
    background-image: url(https://pm2.io/assets/travolta.gif);
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
    min-height: 300px;
    grid-column: 2 / 3;
    grid-row: 1 / 4;
  
  }
  
  @media screen and (max-width: 540px) {
    .notfound {
      padding: 20px 0;
      display: grid;
    
    }
  
    .notfound__title {
      font-size: 80px;
    }
  
    .notfound__subtitle {
      margin-top: 10px;
      font-size: 12px;
    }
    .notfound__btn {
      margin: 30px auto;
      font-size: 12px;
    }
    
    .notfound__img {
      grid-column: 1 / 2;
      grid-row: 3 / 4;
      min-height: 200px;
    
    }
  
  }